import type { MessageEntity } from '../../Client/Managers/MessagesManager'
import { Builder } from '../Builder'
import { InputFile } from './InputFile'

export interface InputMediaVideoPayload {
    type: 'video'
    media: string
    thumbnail?: InputFile | string
    caption?: string
    parse_mode?: string
    caption_entities?: Array<MessageEntity>
    show_caption_above_media?: boolean
    width?: number
    height?: number
    duration?: number
    supports_streaming?: boolean
    has_spoiler?: boolean
}

export class InputMediaVideo extends Builder<InputMediaVideoPayload> {

    public constructor(){
        super({ type: 'video', media: '' })
    }

    public media(value: string): this {
        this.packet.media = value
        return this
    }

    public thumbnail(value?: InputFile | string): this {
        this.packet.thumbnail = value
        return this
    }

    public caption(value?: string): this {
        this.packet.caption = value
        return this
    }

    public parseMode(value?: string): this {
        this.packet.parse_mode = value
        return this
    }

    public captionEntities(value?: Array<MessageEntity>): this {
        this.packet.caption_entities = value
        return this
    }

    public showCaptionAboveMedia(value?: boolean): this {
        this.packet.show_caption_above_media = value
        return this
    }

    public width(value?: number): this {
        this.packet.width = value
        return this
    }

    public height(value?: number): this {
        this.packet.height = value
        return this
    }

    public duration(value?: number): this {
        this.packet.duration = value
        return this
    }

    public supportsStreaming(value?: boolean): this {
        this.packet.supports_streaming = value
        return this
    }

    public hasSpoiler(value?: boolean): this {
        this.packet.has_spoiler = value
        return this
    }

}
